
import Popup from 'vux/src/components/popup/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'


export default {
components: {
Popup, XButton
},
props: ['top_title', 'picture_url', 'show'],
data() {
return {
isShow: false
}
},
methods: {
close() {
this.$emit('close')
}
},
watch: {
show() {
this.isShow = this.show
}
}
}



